import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-region-admin',
  templateUrl: './region-admin.component.html',
  styleUrls: ['./region-admin.component.scss']
})


export class RegionAdminComponent implements OnInit {
  displayDialog: boolean;
  car: any = {};
  selectedCar: any;
  newCar: boolean;

  cars: any[];
  cols: any[];

  constructor() { }

  ngOnInit() {
    this.cars = [
      {code: 'KN1', description: 'Europe'},
      {code: 'KN2', description: 'Middle East'}
    ];

    this.cols = [
      { field: 'code', header: 'Code' },
      { field: 'description', header: 'Description' },
    ];
  }

  showDialogToAdd() {
    this.newCar = true;
    this.car = {};
    this.displayDialog = true;
    this.selectedCar=null;
  }

  save() {
    let cars = [...this.cars];
    if (this.newCar)
      cars.push(this.car);
    else
      cars[this.cars.indexOf(this.selectedCar)] = this.car;

    this.cars = cars;
    this.car = null;
    this.displayDialog = false;
  }

  delete() {
    let index = this.cars.indexOf(this.selectedCar);
    this.cars = this.cars.filter((val, i) => i != index);
    this.car = null;
    this.displayDialog = false;
  }

  onRowSelect(event) {
    this.newCar = false;
    this.car = this.cloneCar(event.data);
    this.displayDialog = true;
  }

  cloneCar(c: any): any {
    let car = {};
    for (let prop in c) {
      car[prop] = c[prop];
    }
    return car;
  }

}
